
import { Options, Vue } from "vue-class-component";

@Options({
  components: {},
})
export default class AdminPortal extends Vue {
  documentId = "";
  errorMessage = "";

  inputAction() {
    this.errorMessage = "";
  }

  submitDocumentId(): void {
    if (!this.documentId) {
      this.errorMessage = "Please Enter Document ID";
      return;
    }
  }

  pasteAction() {
    navigator.clipboard
      .readText()
      .then((cliptext: any) => (this.documentId = cliptext));
  }
}
